import styled from 'styled-components';

export const Title = styled.p`
color: #343F4B;
font-size: 16px;
font-family: 'Roboto';
margin-bottom: 1px;
`;

export const Digite = styled.input`
border: none;
border-bottom: 2px solid #e8e8e8;
padding: 0px 0px 7px 0px;
outline: none;
font-size: 14px;
width: 100%;
font-family: 'Roboto';
color: #777777;
`;

export const Container = styled.div`
display: flex;
flex-direction: column;
align-items: flex-start;
margin-bottom: 10px;
`;